.product_item {
    display: grid;
    grid-template-columns: 1fr;
    border: 1px solid #c4c8dd;
    border-radius: 35px;
    padding: 30px 55px;
    transition: box-shadow 0.4s ease-out;
}

.product_item:hover{
    box-shadow: 0 0 7px 0 gray;
}

.product_img {
    max-height: 300px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.product_price {
    display: grid;
    grid-template-columns: 2fr 1fr;
    margin-top: 0;
}

.product_add {
    display: grid;
    grid-template-columns: 1fr;
    justify-self: end;
    align-self: center;
}

.buy_btn {
    height: fit-content;
    width: 105px;
    margin: 0;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    color: #ffffff;
    font-weight: bold;
    box-shadow: .1em .1em .2em #9D9D9D;
    background: linear-gradient(#778df5, #386df4 30%, #003cff 52%, rgb(10, 69, 215));
}

.buy_btn:active {
    transform: translateY(-1px);
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}

.check_buy_btn {
    height: fit-content;
    width: 105px;
    margin: 0;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    color: #ffffff;
    font-weight: bold;
    box-shadow: .1em .1em .2em #9D9D9D;
    background: linear-gradient(#a1f883, #64da25 30%, #21a914 52%, rgb(85, 178, 4));
}

.check_buy_btn:active {
    transform: translateY(-1px);
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}

.text_price {
    align-self: end;
    font-weight: bold;
    color: #9D9D9D;
}

.price {
    align-self: end;
    font-weight: bold;
    font-size: 26px;
}

.favorite_btn {
    color: #1729f0;
    background-color: #ffffff;
    border: none;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
}

.check_favorite_btn {
    color: #b604e3;
    background-color: #ffffff;
    border: none;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
}