.cart_selected_product {
    display: grid;
    grid-template-columns: 1fr 3fr 2fr;
    border: 1px solid #9D9D9D;
    border-radius: 15px;
    padding: 10px;
    margin-bottom: 15px;
    transition: box-shadow 0.4s ease-out;
    cursor: default;
}

.cart_selected_product:hover {
    box-shadow: 0 0 7px 0 gray;
}

.cart_img {
    width: 70px;
}

.product_title {
    cursor: pointer;
}

.product_price {
    cursor: default;
}

.delete_btn {
    width: fit-content;
    justify-self: end;
    align-self: center;
    padding: 10px 12px 10px 12px;
    color: #ffffff;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: .1em .1em .2em #9D9D9D;
    background: linear-gradient(#FB9575, #F45A38 30%, #EA1502 52%, #F02F17);
}

.delete_btn:active {
    transform: translateY(-1px);
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}