.product_section {
    max-width: 1200px;
    margin: 0 auto;
}
.product_section h2{
    text-align: center;
}

.product_section h3{
    margin: 5px 0;
}

.product_section p{
    margin: 5px 0 15px;
}

.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 10px;
    row-gap: 10px;
    margin-bottom: 30px;
}

.search{
    display: grid;
    grid-template-columns: 1fr;
    align-items: end;
    padding-bottom: 20px;
}

.search_block {
    justify-self: center;
    border: 1px solid #9D9D9D;
    border-radius: 10px;
    padding: 10px;
    width: 95%;
}

.search_block input {
    justify-self: start;
    align-self: center;
    border: none;
    font-size: 20px;
    color: rgba(51, 49, 49, 0.83);

}

.search_block input:focus-visible {
    outline: none;
}

.search_block img {
    width: 20px ;
    margin-right: 15px;
}