.overlay{
    position: fixed;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.404);
    z-index: 1;
}

.cart {
    display: grid;
    grid-template-rows: 0.5fr 6fr 1fr;
    position: absolute;
    width:  700px;
    right: 0;
    height: 100%;
    background-color: #ffffff;
    box-shadow: -10px 0 20px rgba(73, 72, 72, 0.65);
    padding: 0 50px 0 50px;
}

.cart_list{
    overflow: auto;
}

.total_price{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 2fr;
}

.total_price button {
    grid-row: 2/3;
    grid-column: 1/3;
    height: fit-content;
    box-shadow: .1em .1em .2em #9D9D9D;
    background: linear-gradient(#778df5, #386df4 30%, #003cff 52%, rgb(10, 69, 215));
    border: none;
    border-radius: 10px;
    color: white;
    font-size: 22px;
    padding: 20px;
    cursor: pointer;
    align-self: center;
}

.total_price button:active{
    transform: translateY(-1px);
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}

.total_price p{
    font-size: 20px;
    font-weight: bold;
}

.total_price_sum{
    justify-self: end;
}

.total_price_text{
    justify-self: start;
}

.title_block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    cursor: default;
}

.close_btn {
    justify-self: end;
    align-self: center;
    color: #9D9D9D;
    background-color: #ffffff;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 17px;
}