footer {
    display: grid;
    grid-template-columns: 1fr;
    background-color: black;
    max-width: 1200px;
    margin: 0 auto;
    align-items: center;
    position: static;
}

footer div {
    font-size: 30px;
    font-weight: bold;
    padding-left: 50px;
}

footer span {
    color: #ffffffff;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 14px;
    justify-self: center;
    align-self: end;
}

footer a{
    color: #45ff00;
}

footer a:hover{
    text-decoration: underline;
}

footer a:active{
    color: #b604e3;
}

footer a:after{
    color: #b604e3;
}

.logo {
    color: white;
    cursor: default;
}