.banner_section {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    max-width: 1200px;
    margin: 0 auto;
}

.banner{
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    justify-items: end;
    align-items: center;
    background-image: url("/public/img/banner.jpeg");
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    margin-top: 20px;
}

.text_banner {
    color: black;
    font-size: 40px;
    font-weight: bold;
}

.text_banner span {
    color: #001AFF;
}

.banner_btn {
    color: white;
    border: none;
    border-radius: 10px;
    margin-top: 20px;
    padding: 20px 25px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: .1em .1em .2em #9D9D9D;
    background: linear-gradient(#778df5, #386df4 30%, #003cff 52%, rgb(10, 69, 215));
}

.banner_btn:active {
    transform: translateY(-1px);
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}