header {
    display: grid;
    grid-template-columns: 1fr 2fr;
    background-color: black;
    max-width: 1200px;
    margin: 0 auto;
}

.logo {
    color: white;
    cursor: pointer;
    justify-items: center;
    align-items: center;
    padding-left: 50px;
}

nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: end;
    align-items: center;
    padding-right: 30px;
}

.nav_item {
    color: #ffffff;
    font-size: 26px;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    padding-left: 10px;
}

.nav_item:hover {
    color: #1729f0;
}

.cart_title{
    display: grid;
    grid-template-columns: 2fr 0.5fr;
}

.count_cart_items {
    margin-left: 5px;
    color: #64da25;
    font-size:  14px;
    align-self: start;
    padding: 5px;
}